<template>
  <div class="tags d-flex flex-wrap align-items-center">
    <el-tag
      :key="tag"
      v-for="(tag, index) in tagsData"
      closable
      class="tag"
      :disable-transitions="false"
      @close="handleClose(index)"
    >
      {{ tag }}
    </el-tag>
    <el-input
      class="input-new-tag"
      v-if="inputVisible"
      v-model="inputValue"
      ref="saveTagInput"
      size="small"
      @keyup.enter.native="handleInputConfirm"
      @blur="handleInputConfirm"
    >
    </el-input>
    <el-button
      v-else
      v-ripple.400
      icon="bx bx-plus"
      class="button-new-tag no-width"
      size="small"
      @click="showInput"
      >add new</el-button
    >
  </div>
</template>

<script>
export default {
  props: {
    initData: null,
  },
  data() {
    return {
      tagsData: [],
      inputVisible: false,
      inputValue: "",
    };
  },
  mounted() {
    this.tagsData = this.initData;
  },
  methods: {
    handleClose(index) {
      this.tagsData.splice(index, 1);
      this.$emit("change", this.tagsData);
    },

    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.tagsData.push(inputValue);
        this.$emit("change", this.tagsData);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
  },
};
</script>
<style>
.tag {
  margin-bottom: 8px;
  margin-right: 10px;
}
.button-new-tag {
  height: 32px;
  max-width: 100px;
  margin-bottom: 8px !important;
  margin-top: 0px !important;
  line-height: 32px !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.input-new-tag {
  max-width: 100px;
  margin-bottom: 8px;
  margin-right: 10px;
  vertical-align: bottom;
}
</style>
