import Vue from "vue";
import App from "./App.vue";
import store from "./core/store";
import router from "./core/router";
import { appUrl } from "./core/constant/data";
import axios, { axiosSetup } from "./core/axios";
axiosSetup();
Vue.prototype.$axios = axios;

import { Notification, Ripple } from "./core/helper";
Vue.directive("ripple", Ripple);

//import css
import "./assets/css/index.css";
import "vue-cool-lightbox/dist/vue-cool-lightbox.min.css";
import CoolLightBox from "vue-cool-lightbox";
Vue.use(CoolLightBox);

import VueLazyload from "vue-lazyload";
const theme = localStorage.getItem("theme");
var isDark = false;
if (theme) {
  isDark = theme == "dark";
}
const loader = isDark ? "/image/loader-dark.svg" : "/image/loader.svg";
const error = isDark ? "/image/error-dark.png" : "/image/error.png";
import canvas from "./core/directive/canvas";
Vue.use(canvas);
Vue.use(VueLazyload, {
  error: appUrl + error,
  loading: appUrl + loader,
});
Vue.use(require("vue-moment"));

import VueApexCharts from "vue-apexcharts";
Vue.use(VueApexCharts);
Vue.component("apexchart", VueApexCharts);

import ElementUI from "element-ui";
import locale from "element-ui/lib/locale/lang/en";
Vue.use(ElementUI, { locale });

import smalltalk from "smalltalk";
Vue.prototype.$smalltalk = smalltalk;

import Transitions from "vue2-transitions";
Vue.use(Transitions);

//languages
import i18n from "./core/i18n";
Vue.config.productionTip = false;

new Vue({
  render: (h) => h(App),
  router,
  i18n,
  store,
  created() {
    Vue.prototype.$noti = new Notification(this.$notify);
    if (localStorage.getItem("isLoggedIn")) {
      this.$store.commit("setIsLoggedIn", true);
    } else {
      this.$store.commit("setIsLoggedIn", false);
    }
  },
  mounted() {

    //fix el-select
let fixElSelect = function () {
	document.querySelectorAll('.el-select[data-is-search="true"]:hover').forEach(() => {
		let elInput = document.querySelector('.el-select[data-is-search="true"]:hover input[readonly]');
		if (elInput) {
			elInput.readOnly = false;
			elInput.blur();
			elInput.focus();
		}
	});
};
document.addEventListener("focusin", fixElSelect);
document.addEventListener("click", fixElSelect);
document.addEventListener("touchstart", fixElSelect);

  },
  watch: {},
}).$mount("#app");
