
<template>
  <div class="bottom-tab md-hide lg-hide">
    <router-link
      v-for="menuItem in filteredMenu"
      :key="menuItem.name"
      active-class="active"
      :id="menuItem.class"
      class="menuItem text align-items-center justify-content-between"
      :class="
        `${menuItem.moreClass} ` +
        (active == menuItem.class && 'router-link-exact-active ')
      "
      :style="`display:${
        menuItem.role
          ? menuItem.role === store.type
            ? 'flex'
            : 'none'
          : 'flex'
      }`"
      :to="menuItem.route"
    >
      <div class="flex-center flex-column">
        <i :class="menuItem.icon" style="z-index: 100" class="mt-1"></i>
        <p style="font-size: 10px" class="mb-0 mt-1 text-center">
          {{ menuItem.name }}
        </p>
      </div>
    </router-link>
  </div>
</template>
<script>
import menu from "../../core/constant/menu";
import { mapState } from "vuex";
export default {
  data: () => ({
    isReduced: false,
    active: "/",
  }),
  computed: {
    ...mapState(["store"]),
    filteredMenu() {
      var menus = [];
      menu.forEach((item) => {
        if (item.type) {
          if (item.type == this.store.type) {
            menus.push(item);
          }
        } else {
          menus.push(item);
        }
      });
      return menus;
    },
  },
  components: {},
  watch: {
    $route(to) {
      this.active = to.meta.class;
      this.changeRoute(this.active);
    },
  },

  mounted() {
    this.active = this.$route.meta.class;
    this.changeRoute(this.active);
  },
  methods: {
    changeRoute(id) {
      const activeTabElement = document.getElementById(id);
      if (activeTabElement)
        document.getElementById("active-tab").style.top =
          activeTabElement.offsetTop - 25 + "px";
    },
  },
};
</script>

<style >
.bottom-tab {
  position: fixed;
  z-index: 10000000000;
  bottom: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  background: rgb(var(--background-1));
  box-shadow: 2px 2px 20px rgb(0 0 0 / 20%);
}
/* ---------------------------------------------------------------- */
/* mobile */
@media screen and (max-width: 600px) {
}

/* tablet */
@media screen and (min-width: 600px) and (max-width: 992px) {
}

/* desktop */
@media (min-width: 992px) {
}
</style>