<template>
  <div class="w-100">
    <el-input
      class="main-search"
      v-model="query"
      prefix-icon="bx bx-search"
      @change="getData"
      style="background: transparent !important"
      placeholder="Search anything..."
      :class="searching && 'searching'"
    >
    </el-input>

    <collapse-transition>
      <div class="search_results position-absolute" v-if="searching">
        <div
          style="height: 30vh"
          v-if="!loading && length === 0"
          class="d-flex align-items-center justify-content-center"
        >
          <el-empty description="No data found!"></el-empty>
        </div>
        <div class="el-skeleton is-animated" v-if="loading">
          <div class="flex-align-center mb-2">
            <el-skeleton-item
              variant="image"
              style="width: 50px; height: 50px; border-radius: 6px"
            />
            <div style="width: 150px" class="ml-2 block">
              <el-skeleton :rows="2" animated />
            </div>
          </div>
          <div class="b-divider my-2"></div>
          <div class="flex-align-center mb-2">
            <el-skeleton-item
              variant="image"
              style="width: 50px; height: 50px; border-radius: 6px"
            />
            <div style="width: 150px" class="ml-2 block">
              <el-skeleton :rows="2" animated />
            </div>
          </div>
        </div>
        <div class="searched-data-box" v-if="!loading && length > 0">
          <div v-for="(items, type, index) in data" :key="type">
            <div class="searched-data" v-if="items.length > 0">
              <p class="font-weight-bold">
                {{ type == "User" ? "Staffs" : type }}
              </p>

              <div
                @click="goTo(type, item), (showSearch = false)"
                class="
                  searched-items
                  primaryHover
                  pointer
                  mb-2
                  flex-align-center
                "
                v-for="(item, n) in items"
                :key="
                  (item.name ||
                    item.first_name + ' ' + item.last_name ||
                    item.order_no) + n
                "
              >
                <img
                  v-lazy="
                    mediaUrl +
                    ((item.images && item.images[0].image) ||
                      item.avatar ||
                      item.image ||
                      '/media/default.png')
                  "
                  style="
                    border-radius: 6px;
                    object-fit: cover;
                    width: 50px;
                    height: 50px;
                  "
                  alt=""
                />
                <p class="ml-2">
                  {{
                    item.name ||
                    item.order_no ||
                    item.first_name + " " + item.last_name
                  }}
                </p>
              </div>
              <div class="b-divider my-2" v-if="index !== 5"></div>
            </div>
          </div>
        </div>
      </div>
    </collapse-transition>
  </div>
</template>

<script>
import { appUrl, mediaUrl } from "../../core/constant/data";
export default {
  data() {
    return {
      showSearch: false,
      mediaUrl: mediaUrl,
      query: "",
      appUrl: appUrl,
      length: 0,
      loading: false,
      searching: false,
    };
  },
  watch: {},
  methods: {
    getData() {
      this.searching = true;
      this.loading = true;
      var query = null;
      if (this.query === "" || !this.query) {
        query = "Null";
        this.searching = false;
      } else {
        query = this.query;
      }
      this.$axios
        .get("search" + "/?query=" + query)
        .then((response) => {
          this.length = response.data.overall_total;
          this.data = response.data.results;
          this.loading = false;
        })
        .catch((err) => {
          this.$error.raise(err);
          this.loading = false;
        });
    },
    goTo(type, item) {
      if (type === "User") {
        this.$router.push({
          name: "Edit Staff",
          params: { id: item.id },
        });
      } else {
        this.$router.push({
          name: "Edit " + type,
          params: { id: item.id },
        });
      }
      this.searching = false;
      this.data = {};
      this.loading = false;
      this.query = "";
    },
  },
};
</script>

<style >
.main-search {
  z-index: 1;
  transition: 0.3s all;
}
.main-search .el-input__inner {
  z-index: 1;
  width: 350px;
  transition: 0.3s all ease-in-out;
  border: none !important;
  box-shadow: none !important;
  background: rgba(var(--background-1)) !important;
}

.searching.el-input {
  z-index: 100000001 !important;
}

.search_results {
  max-height: 80vh;
  margin-top: -8px;
  padding-top: 15px !important;
  overflow-y: auto;
  width: 350px;
  border-radius: 0px 0px 0.85rem 0.85rem !important;
  padding: 5px 15px !important;
  z-index: 100000001 !important;
  -webkit-animation-duration: 250ms !important;
  animation-duration: 250ms !important;
  background: rgba(var(--background-1), 1) !important;
  box-shadow: 0 30px 30px -25px rgb(0 0 0 / 25%);
}
</style>