export default {
  setUser(state, data) {
    state.user = data;
  },
  setAccess(state, data) {
    state.access = data;
  },
  setRefresh(state, data) {
    state.refresh = data;
  },
  setIsReady(state, data) {
    state.isReady = data;
  },
  setStore(state, data) {
    state.store = data;
  },
  setIsDark(state, data) {
    state.isDark = data;
  },
  setCenter: (state, data) => {
    state.center = data;
  },
  setCurrentLocation: (state, data) => {
    state.currentLocation = data;
  },
  toggleUploader(state) {
    state.openUploader = !state.openUploader;
  },
  toggleDiscard(state) {
    state.openDiscard = !state.openDiscard;
  },
  setIsLoggedIn(state, data) {
    state.isLoggedIn = data;
    state.showSidebar = data;
  },
  setLoading(state, data) {
    state.loading = data;
  },
  setLang(state, data) {
    state.lang = data;
  },
  setSidebar(state, data) {
    state.showSidebar = data;
  },
  setLocations(state, data) {
    state.locations = data;
  },
  addLocation(state, data) {
    state.locations = [data, ...state.locations];
  },
  updateLocation(state, data) {
    for (var i in state.locations) {
      if (parseInt(state.locations[i].id) == parseInt(data.id)) {
        state.locations[i] = data;
        break;
      }
    }
  },
  setOptions(state, data) {
    state.options = data;
  },
  
  setFile(state, data) {
    state.file = data;
  },
  setFiles(state, data) {
    var files={
      count:data.count,
      current_page:data.current_page,
      next:data.next,
      previous:data.previous,
      total_pages:data.total_pages,
      results:[...state.files.results,...data.results]
    }
    state.files =files;
  },
  setGettingFiles(state, data) {
    state.isGettingFiles = data;
  },
  addFile(state, data) {
    var files={
      count:data.count,
      current_page:data.current_page,
      next:data.next,
      previous:data.previous,
      total_pages:data.total_pages,
      results:[...data,...state.files.results]
    }
    state.files =files;
  },
  removeFile(state, data) {
    var files={
    ...state.files,
      results:state.files.results.filter((image) => !data.includes(image.id))
    }
    state.files = files;
  },
  setActiveSideBar(state, data) {
    state.activeSideBar = data;
  },
  setRoles(state, data) {
    state.roles = data;
  },
  setInvoiceSetting(state, data) {
    state.invoiceSetting = data;
  },
};
