<template>
  <div id="app" class="container-fluid">
    <toggleTheme class="auth-toggle-theme" v-show="!isLoggedIn" />
    <navbar v-if="isLoggedIn" />

    <sidebar
      v-if="store"
      :class="showSidebar ? 'show-sidebar' : 'hide-sidebar'"
      class="sm-hide"
    />
    <div
      class="content-view"
      :class="showSidebar ? 'conten-has-sidebar' : 'has-no-sidebar'"
    >
      <router-view
        :key="$route.fullPath"
        class="router_view mx-0 w-100"
        v-show="isReady"
      >
      </router-view>
    </div>
    <bottomTab />
    <slide-y-down-transition>
      <el-button
        type="primary"
        circle
        class="no-width scroll-to-top"
        v-if="isLoggedIn && showScrollToTop"
        @click="scrollToTop"
        icon="bx bxs-chevron-up"
      ></el-button>
    </slide-y-down-transition>

    <div
      v-if="!isReady && isLoggedIn"
      class="row router_view loader"
      key="loader"
      :class="
        isLoggedIn && (showSidebar ? 'conten-has-sidebar' : 'has-no-sidebar')
      "
    >
      <div
        v-loading="!isReady"
        style="min-height: calc(100vh - 155px); margin-top: 80px"
      ></div>
    </div>
  </div>
</template>

<script>
import { sidebar, navbar, toggleTheme, bottomTab } from "./components/common";
import { mapState, mapGetters } from "vuex";
export default {
  data() {
    return {
      online: navigator.onLine,
      is_expired: false,
      showScrollToTop: false,
    };
  },
  computed: {
    ...mapState(["showSidebar", "access", "isLoggedIn", "store", "isReady"]),
    ...mapGetters(["has_user", "isLg", "isMd", "isSm"]),
    has_type() {
      return this.store ? (this.store.type ? true : false) : false;
    },
  },
  mounted() {},
  created() {
    this.checkIsLoggedInAndGetData();
    window.addEventListener("scroll", this.handleScroll);
  },
  watch: {
    access() {
      this.checkIsLoggedInAndGetData();
    },
    $route(to) {
      this.$store.commit("setActiveSideBar", to.meta.index);
      localStorage.setItem("active", to.meta.index);
      this.showScrollToTop = false;
      if (!this.isLg) {
        this.$store.commit("setSidebar", false);
      }
    },
    has_user(value) {
      if (value) {
        this.getData();
      }
    },
    isReady(val) {
      if (val) {
        const loader = document.querySelector(".page-loader");
        setTimeout(() => {
          loader.className += " hidden";
          setTimeout(() => {
            loader.className += " display_none";
            if (!this.isLg) {
              this.$store.commit("setSidebar", false);
            }
          }, 500);
        }, 500);
      }
    },
  },
  components: {
    sidebar,
    navbar,
    toggleTheme,
    bottomTab,
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    getData() {
      this.$store.dispatch("getFile", 1);
    },
    checkIsLoggedInAndGetData() {
      if (this.access) {
        this.$store.dispatch("getAuth");
        if (this.isLg) {
          this.$store.commit("setSidebar", true);
        } else {
          this.$store.commit("setSidebar", false);
        }
        if (this.has_user) {
          this.getData();
        }
      } else {
        this.$store.commit("setSidebar", false);
        const loader = document.querySelector(".page-loader");
        setTimeout(() => {
          loader.className += " hidden";
          setTimeout(function () {
            loader.className += " display_none";
          }, 500);
        }, 500);
      }
    },
    handleScroll(e) {
      this.showScrollToTop = e.target.scrollingElement.scrollTop > 500;
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
};
</script>
<style>
.page-fade-enter-active,
.page-fade-leave-active {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.page-fade-enter-from,
.page-fade-leave-to {
  opacity: 0;
}

.content-view {
  transition: width 0.3s ease-in-out, margin-left 0.3s ease-in-out;
  background: rgb(var(--background-2));
  min-height: 100vh;
}
.scroll-to-top {
  position: fixed;
  right: 15px;
  bottom: 70px;
  z-index: 1000000000 !important;
}
.router_view {
  scroll-behavior: smooth;
  height: 100%;
  overflow-y: scroll !important;
  overflow-x: hidden !important;
  padding-top: 65px !important;
  padding: 0px 15px 15px;
  padding-bottom: 35px !important;
  align-items: flex-start !important;
  display: block !important;
}

.has-no-sidebar {
  width: calc(100vw - 10px) !important;
  margin-left: 5px !important;
  padding-left: 0px !important;
  padding-right: 0px !important;
}
.loader {
  position: absolute;
  z-index: 999;
  top: 0px;
}
img.auth-logo {
  position: absolute;
  width: 140px;
  top: 10px;
  left: 10px;
}
.auth-img {
  height: 55vh;
}
div#login-page {
  padding: 5px !important;
}
.login-box {
  width: 100% !important;
  height: calc(100vh - 20px);
  padding: 0px 50px !important;
}
</style>
