import moment from "moment";
export default {
  checkIsReady: (state) => {
    if (state.user && state.locations && state.store) {
      state.isReady = true;
    }
  },
  isDark: (state) => {
    return state.isDark;
  },
  getAccess: (state) => {
    return state.access;
  },
  isSetupFinished: (state) => {
    var isReady = false;
    if (
      state.store &&
      state.store.settings &&
      state.invoiceSetting &&
      state.store.settings.tax_type &&
      state.store.settings.currency &&
      state.store.settings.primary_color &&
      state.locations
    ) {
      isReady = true;
    }
    return isReady;
  },
  has_user: (state) => (state.user ? true : false),
  has_locations: (state) => {
    return state.locations.length > 0 ? true : false;
  },
  getLocation: (state) => (name) => {
    var location = state.locations.filter((data) => {
      return data.name === name;
    })[0];
    return location;
  },
  isStoreExpired: (state) => {
    // return false;
    if (state.store) {
      return moment().isAfter(state.store.plan.exp_date);
    } else {
      return false;
    }
  },
  isLg: () => {
    return window.matchMedia("(min-width: 1200px)").matches;
  },
  isMd: () => {
    return window.matchMedia("(min-width: 768px) and (max-width: 1200px)")
      .matches;
  },
  isSm: () => {
    return window.matchMedia("(max-width: 600px)").matches;
  },
};
