<template>
  <nav
    class="flex-align-center"
    :class="showSidebar ? 'showSidebar-nav' : 'hideSidebar-nav'"
    id="main-nav"
    v-if="user"
  >
    <div class="row mx-0 w-100">
      <div class="flex-x-between px-0 col-12">
        <div class="flex-align-center">
          <el-button
            icon="bx bx-refresh"
            type="primary"
            circle
            class="mr-2 ml-0 flat no-width"
            v-ripple.400
            @click="reload"
          ></el-button>
          <el-button
            icon="bx bx-menu"
            type="primary"
            circle
            class="mr-2 ml-0 flat sm-hide no-width"
            v-ripple.400
            @click="$store.commit('setSidebar', !showSidebar)"
          ></el-button>
          <toggleTheme class="mr-2 m-0" />
          <!-- <el-button
            icon="bx bx-support"
            type="primary"
            circle
            @click="goToMessenger"
            class="mr-2 ml-0 flat no-width"
            v-ripple.400
          ></el-button> -->
          <!-- <Search /> -->
        </div>
        <div class="flex-end">
          <el-dropdown trigger="click" class="mx-2" @command="handleCommand">
            <div class="flex-end">
              <div class="flex-end mr-2 sm-hide align-items-end flex-column">
                <p class="text-bold">
                  {{ user.first_name + " " + user.last_name }}
                </p>
                <p class="sm-text gray">{{ user.email }}</p>
              </div>
              <div class="avatar" v-if="user.avatar">
                <el-avatar :src="mediaUrl + user.avatar"></el-avatar>
                <img :src="mediaUrl + user.avatar" alt="" class="img-after" />
              </div>

              <el-avatar v-if="!user.avatar">{{ user.first_name }}</el-avatar>
            </div>
            <el-dropdown-menu slot="dropdown" class="main_dropdown">
              <el-dropdown-item icon="bx bx-user" command="profile"
                >My Profile</el-dropdown-item
              >
              <el-dropdown-item icon="bx bx-cog" command="settings"
                >Settings</el-dropdown-item
              >
              <el-dropdown-item command="logout" icon="bx bx-log-in-circle"
                >Logout</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import { mediaUrl } from "../../core/constant/data";
// import Search from "./search.vue";
import toggleTheme from "./toggleTheme.vue";

export default {
  data() {
    return {
      mediaUrl: mediaUrl,
      search: "",
      activeTooltip1: false,
    };
  },
  components: { toggleTheme },
  computed: {
    ...mapState(["showSidebar", "user"]),
    ...mapGetters(["isLg"]),
  },
  methods: {
    reload() {
      window.location.reload();
    },
    goToMessenger() {
      window.open("https://m.me/106339588511793", "_blank").focus();
    },
    handleCommand(command) {
      if (command === "logout") {
        this.$store.dispatch("logout");
      }
      if (command === "profile") {
        this.$router.push({ name: "My Account" });
      }
      if (command === "settings") {
        this.$router.push({ name: "Settings" });
      }
    },
  },
};
</script>
