import axios from "../axios";
import router from "../router";
export default {
  logIn: async ({ commit }, payload) => {
    const loginUrl = "login/";
    try {
      await axios.post(loginUrl, payload).then((response) => {
        if (response.status === 200) {
          commit("setRefresh", response.data.refresh);
          commit("setAccess", response.data.access);
          localStorage.setItem("access", response.data.access);
          localStorage.setItem("refresh", response.data.refresh);
          localStorage.setItem("isLoggedIn", true);
          commit("setIsReady", false);
          router.push({ name: "Home" });
        }
      });
    } catch (err) {
      return err;
    }
  },
  logout({ commit }) {
    localStorage.removeItem("access");
    localStorage.removeItem("refresh");
    localStorage.removeItem("isLoggedIn");
    commit("setIsLoggedIn", false);
    commit("setAccess", null);
    commit("setRefresh", null);
    router.push({ name: "Login" });
  },
  getAuth: async ({ commit }) => {
    await axios
      .get("auth/")
      .then((response) => {
        commit("setUser", response.data);
        commit("setIsLoggedIn", true);
        commit("setStore", response.data.store);
      })
      .catch((err) => {
        return err;
      });
  },
  getRefreshToken: ({ dispatch, commit }) => {
    if (localStorage.getItem("refresh")) {
      axios
        .post("login/refresh/", { refresh: localStorage.getItem("refresh") })
        .then((response) => {
          var access = response.data.access;
          localStorage.setItem("isLoggedIn", true);
          localStorage.setItem("access", access);
          commit("setAccess", access);
          commit("setIsReady", false);
          // router.push({ name: "Home" });
        })
        .catch((err) => {
          if (err.response.status == 401) {
            dispatch("logout");
          }
        });
    } else {
      dispatch("logout");
    }
  },
  getFile: async ({ commit }, page) => {
    commit('setGettingFiles',true)
    await axios
      .get(`file/list/?page=${page}&page_size=40`)
      .then((response) => {
        commit('setFiles',response.data)
        commit('setGettingFiles',false)
      })
      .catch((err) => {
        return err;
      });
  },
};
