<template>
  <div class="row">
    <div class="col-12">
      <div class="row media-box mx-0" v-if="multiple">
        <div
          class="col-3 px-0 mb-2 selected-images light-box-img"
          v-for="(image, imageIndex) in selected"
          :key="image.image + imageIndex"
        >
          <span class="remove-image pointer" @click="removeImage(imageIndex)"
            ><i class="bx bx-x"></i
          ></span>
          <i
            class="bx bx-expand-alt pointer"
            @click="openLightBox(image.image)"
          ></i>
          <img :src="mediaUrl + image.image" @load="setHeight" alt="" />
        </div>

        <div class="col-3 pointer file-uploader-box mb-2 px-0" key="uploader">
          <div
            class="file-uploader"
            style="width: 100% !important; max-width: 160px"
            @click="openUploader = !openUploader"
          >
            <i class="bx bx-camera"></i>
            <p>Add Image</p>
          </div>
        </div>
      </div>

      <div
        class="row media-box mx-0"
        v-if="!multiple && selected.length > 0 && !small"
      >
        <div
          class="px-0 mb-2 selected-images light-box-img"
          :class="single ? 'col-12' : 'col-3'"
          v-for="(image, imageIndex) in selected"
          :key="image.image + imageIndex"
        >
          <span class="remove-image pointer" @click="removeImage(imageIndex)"
            ><i class="bx bx-x"></i
          ></span>
          <span
            class="open-light-box pointer"
            @click="openLightBox(image.image)"
          ></span>
          <img :src="mediaUrl + image.image" @load="setHeight" alt="" />
        </div>
      </div>

      <div
        class="row media-box mx-0"
        v-if="!multiple && selected.length < 1 && !small"
      >
        <div
          class="pointer mb-2 file-uploader-box px-0"
          :class="single ? 'col-12' : 'col-3'"
        >
          <div
            class="file-uploader"
            style="width: 100% !important; max-width: 160px"
            @click="openUploader = !openUploader"
          >
            <i class="bx bx-camera"></i>
            <p class="normal-text">Add Image</p>
          </div>
        </div>
      </div>

      <div class="row mx-0" v-if="small">
        <div class="col-12 p-0 selected-images" v-if="selected.length == 1">
          <img
            :src="mediaUrl + selected[0].image"
            @click="openUploader = !openUploader"
            style="height: 45px !important; width: 45px !important"
            alt=""
          />
        </div>

        <div
          class="col-12 pointer file-uploader-box"
          key="uploader"
          v-if="selected.length == 0"
        >
          <div
            class="file-uploader"
            style="height: 45px !important; width: 45px !important"
            @click="openUploader = !openUploader"
          >
            <i class="bx bx-camera"></i>
            <p class="sm-text">Add Image</p>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openUploader"
      id="uploader"
    >
      <div
        class="row uploader-box"
        ref="content"
        style="height: 100% !important"
        v-loading="removing"
      >
        <div class="col-12 flex-x-between">
          <div class="flex-align-center">
            <p
              class="mx-0 table-tab"
              :class="type === 'media' && 'active-table-tab'"
              @click="type = 'media'"
            >
              All media
            </p>
            <p
              class="mx-0 table-tab"
              :class="type === 'upload' && 'active-table-tab'"
              @click="type = 'upload'"
            >
              Upload
            </p>
          </div>
          <div class="flex-end">
            <el-button
              class="mx-0 mb-3 mb-sm-1 flat lg-hide w-100"
              type="primary"
              @click="selectImage"
            >
              Done
            </el-button>
          </div>
        </div>
        <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div>

        <div
          class="
            col-8 col-md-12 col-sm-12
            show-scroll
            pt-3
            images-select-view
            right-border
          "
          v-if="type == 'media'"
          @scroll="onScroll"
        >
          <div class="row px-3 px-sm-1" v-if="files">
            <div
              v-for="(data, imageIndex) in files.results"
              v-bind:key="data.image"
              class="col-3 px-1 mb-3 light-box-img image-box"
            >
              <i
                class="bx bx-expand-alt pointer"
                @click="openLightBox(data.image), (active = true)"
              ></i>
              <div
                @click="
                  (data.isSelected = !data.isSelected), changeSelect(data)
                "
                class="image-check animated fadeIn"
                :style="
                  'animation-delay:' +
                  (imageIndex + 1) * 45 +
                  'ms;animation-duration:0.8s;'
                "
              >
                <img
                  v-lazy="mediaUrl + data.image"
                  class="file-image"
                  :class="data.isSelected && 'selected'"
                  style="width: 100% !important"
                  @load="setHeight"
                />
              </div>
            </div>

            <div class="col-12 flex-center" v-if="isGettingFiles">
              <p>Loading ...</p>
            </div>
          </div>

          <div
            v-else
            style="height: 65vh"
            class="d-flex align-items-center justify-content-center"
          >
            <el-empty description="No data found!"></el-empty>
          </div>
        </div>
        <div
          class="
            col-4
            md-hide
            d-flex
            sm-hide
            justify-content-between
            flex-column
          "
          v-if="type == 'media'"
        >
          <div class="row w-100 mx-0 selected-view">
            <div class="col-12 flex-x-between px-0">
              <p class="mb-2 mt-3 font-weight-bold">Preview</p>
              <p
                class="danger mb-2 mt-3 hover-underline pointer"
                v-if="selected.length > 0 && type == 'media'"
                @click="removeFiles"
              >
                Delete
              </p>
            </div>
            <div
              class="row px-0 w-100 mx-0"
              style="max-height: 53vh; overflow-y: auto"
            >
              <div
                class="col-4 px-1 mb-3"
                v-for="image in selected"
                :key="image.image"
              >
                <img
                  :src="mediaUrl + image.image"
                  class="simple-image"
                  alt=""
                  style="width: 100% !important"
                  @load="setHeight"
                />
              </div>
            </div>
          </div>

          <footer class="w-100">
            <div class="divider mt-0 mb-2"></div>
            <div class="d-flex justify-content-between">
              <el-button
                class="mx-0 mb-3 w-100"
                type="primary"
                @click="selectImage"
              >
                Done
              </el-button>
            </div>
          </footer>
        </div>
        <div class="col-12" v-if="type == 'upload'">
          <div class="row px-3">
            <div
              class="col-12 d-flex justify-content-center align-items-center"
              style="height: 68vh"
            >
              <div
                class="
                  file-uploader
                  d-flex
                  justify-content-center
                  align-items-center
                "
                v-loading="loading"
                style="width: 70%"
              >
                <input
                  type="file"
                  ref="fileInput"
                  accept="image/*"
                  class="uploader-file-input pointer"
                  @change="onFileChange"
                  name=""
                  multiple
                />
                <div
                  class="
                    flex-column
                    d-flex
                    justify-content-center
                    align-items-center
                  "
                >
                  <p class="gray mb-2">Drag images here to upload Or</p>
                  <el-button
                    v-ripple.400
                    type="primary"
                    @click="$refs.fileInput.click()"
                    >Add images</el-button
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>

    <CoolLightBox
      :items="lightBoxFile"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mediaUrl } from "../../core/constant/data";
export default {
  props: {
    small: {
      type: Boolean,
      default: false,
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    single: {
      type: Boolean,
      default: false,
    },
    initialData: {
      type: Array,
    },
  },
  data() {
    return {
      openUploader: false,
      mediaUrl: mediaUrl,
      type: "media",
      removing: false,
      newImages: [],
      lightBoxFile: [],
      index: null,
      selected: [],
      loading: false,
      media_key: {
        cloud_name: "osassist",
        api_key: "512476284172212",
        upload_preset: "xmzp1v5t",
      },
      resFiles: [],
    };
  },
  watch: {
    selected() {
      setTimeout(() => {
        this.setUploaderHeight();
      }, 100);
    },
    file(val) {
      if (val) {
        this.files.results.forEach((image) => {
          image.isSelected = false;
        });
        this.setUploaderHeight();
      }
    },
    store(val) {
      if (val.fbLink) {
        this.media_key = JSON.parse(val.fbLink);
      }
    },
  },
  computed: {
    ...mapState(["files", "store", "isGettingFiles"]),
  },
  mounted() {
    if (this.initialData) {
      this.selected = this.initialData;
    }
    if (this.files) {
      this.files.results.forEach((image) => {
        image.isSelected = false;
      });
      this.setUploaderHeight();
    }
    if (this.store.fbLink) {
      this.media_key = JSON.parse(this.store.fbLink);
    }
  },
  methods: {
    openLightBox(image) {
      this.lightBoxFile = [this.mediaUrl + image];
      this.index = 0;
    },
    setUploaderHeight() {
      const element = document.querySelector(".file-uploader");
      if (element) {
        element.style.height = element.clientWidth + "px";
      }
    },
    changeSelect(data) {
      if (this.multiple) {
        this.selected = this.files.results.filter((image) => {
          return image.isSelected;
        });
      } else {
        this.files.results.filter((image) => {
          if (image.image !== data.image) {
            image.isSelected = false;
          }
        });
        this.selected = [data];
      }
    },
    setHeight(e) {
      e.path[0].style.height = e.path[0].clientWidth + "px";
    },
    async compress(file, index, totalLength) {
      const fileName = file.name;
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (event) => {
        const img = new Image();
        img.src = event.target.result;

        (img.onload = () => {
          var width = img.width;
          var height = img.height;
          if (img.width > 1000) {
            width = img.width / 10;
            height = img.height / 10;
          }

          const elem = document.createElement("canvas");
          elem.width = width;
          elem.height = height;
          const ctx = elem.getContext("2d");
          // img.width and img.height will contain the original dimensions
          ctx.drawImage(img, 0, 0, width, height);
          ctx.canvas.toBlob(
            async (blob) => {
              const resFile = new File([blob], fileName, {
                type: "image/jpeg",
                lastModified: Date.now(),
              });
              const formData = new FormData();
              formData.append("file", resFile);
              formData.append("upload_preset", this.media_key.upload_preset);
              formData.append("api_key", this.media_key.api_key);
              formData.append("timestamp", (Date.now() / 1000) | 0);

              var response = await fetch(
                `https://api.cloudinary.com/v1_1/${this.media_key.cloud_name}/image/upload`,
                {
                  method: "POST",
                  body: formData,
                }
              );
              const img = await response.json();

              const { data } = await this.$axios.post("files/", {
                data: {
                  image: img.secure_url,
                },
              });
              this.$store.commit("addFile", [data]);

              if (index === totalLength - 1) {
                this.loading = false;
                this.type = "media";
              }
            },
            "image/jpeg",
            1
          );
        }),
          (reader.onerror = (error) => console.log(error));
      };
    },
    async onFileChange(e) {
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        alert("The file uploaded need to be -jpeg,jpg,png");
        return false;
      }
      this.loading = true;
      var files = e.target.files;
      var totalLength = files.length;
      files.forEach(async (file, index) => {
        this.compress(file, index, totalLength);
      });
    },
    // uploadImageToServer(img) {
    //   this.$axios
    //     .post("files/", { data: img })
    //     .then(() => {
    //       this.$store.commit("addFile", [img]);
    //       this.type = "media";
    //     })
    //     .catch((err) => {
    //       this.loading = false;
    //       this.$noti.display({
    //         message: err,
    //         status: "error",
    //         color: "error",
    //       });
    //     });
    // },
    selectImage() {
      this.$emit("completed", this.selected);
      this.openUploader = false;
    },
    removeFiles() {
      if (this.selected.length > 0) {
        this.removing = true;
        var data = [];
        this.selected.forEach((image) => {
          data.push(image.id);
        });
        this.$axios.post("remove-files/", { data }).then(() => {
          this.$store.commit("removeFile", data);
          this.selected = [];
          this.removing = false;
          this.$noti.display(
            "Cool! File has been successfully deleted.",
            "",
            "normal",
            "success"
          );
        });
      }
    },
    removeImage(index) {
      this.selected.splice(index, 1);
      this.$emit("completed", this.selected);
    },
    onScroll(e) {
      if (e.target.offsetHeight + e.target.scrollTop >= e.target.scrollHeight) {
        if (!this.isGettingFiles) {
          this.$store.dispatch("getFile", this.files.next);
        }
      }
    },
  },
};
</script>

<style >
span.open-light-box.pointer {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}
</style>