export default class Notification {
  constructor(notification) {
    this.notification = notification;
  }

  /**
   * displaying notification message
   * @param {String} message Message body
   * @param {String} title title of notification
   * @param {String} status status of notification
   * @param {String} color color of notification
   *
   */
  display(params = { title: "", status: "normal", color: "success" }) {
    if (params.status === "error") {
      if (
        typeof params.message.response.data === "object" &&
        !params.message.response.data.byteLength
      ) {
        for (var key in params.message.response.data) {
          this.notification({
            type: params.color,
            title: key,
            message:
              typeof params.message.response.data[key] === "string"
                ? params.message.response.data[key]
                : params.message.response.data[key][0],
            position: "bottom-right",
          });
        }
      } else {
        this.notification({
          type: params.color,
          title: "status - " + params.message.response.status,
          message: params.message.response.statusText,
          position: "bottom-right",
        });
      }
    } else {
      this.notification({
        type: params.color,
        title: params.title,
        message: params.message,
        position: "bottom-right",
      });
    }
  }
}
