
<template>
  <div class="sidebar">
    <div class="sidebar-header">
      <img src="/white-logo.png" class="logo-img" alt="" />
    </div>
    <div class="sidebar-items show-scroll">
      <div class="active-tab" id="active-tab">
        <!-- <div class="top-layers">
          <div class="top-layer2"></div>
          <div class="top-layer1"></div>
        </div> -->
        <!-- <div class="middle-layer">
          <div class="active-sidebar-border"></div>
        </div>
        <div class="bottom-layers">
          <div class="bottom-layer1"></div>
          <div class="bottom-layer2"></div>
        </div> -->
        <div class="active-sidebar-item-bg"></div>
      </div>
      <router-link
        v-for="menuItem in filteredMenu"
        :key="menuItem.name"
        active-class="active"
        :id="menuItem.class"
        class="menuItem text align-items-center justify-content-between"
        :class="
          `${menuItem.moreClass} ` +
          (active == menuItem.class && 'router-link-exact-active ')
        "
        :style="`display:${
          menuItem.role
            ? menuItem.role === store.type
              ? 'flex'
              : 'none'
            : 'flex'
        }`"
        :to="menuItem.route"
      >
        <div class="d-flex w-100 align-items-center">
          <i
            :class="menuItem.icon"
            style="z-index: 100"
            class="mr-2 primary"
          ></i>
          <span style="z-index: 100" class="mb-0 title">
            {{ menuItem.name }}
          </span>
        </div>
      </router-link>
    </div>
  </div>
</template>
<script>
import menu from "../../core/constant/menu";
console.log("🚀 ~ file: sidebar.vue:57 ~ menu", menu);
import { mapState } from "vuex";
export default {
  data: () => ({
    isReduced: false,
    active: "/",
  }),
  computed: {
    ...mapState(["showSidebar", "store"]),
    filteredMenu() {
      var menus = [];
      menu.forEach((item) => {
        if (item.type) {
          if (item.type == this.store.type) {
            menus.push(item);
          }
        } else {
          menus.push(item);
        }
      });
      return menus;
    },
  },
  components: {},
  watch: {
    $route(to) {
      this.active = to.meta.class;
      this.changeRoute(this.active);
    },
  },

  mounted() {
    this.active = this.$route.meta.class;
    this.changeRoute(this.active);
  },
  methods: {
    changeRoute(id) {
      const activeTabElement = document.getElementById(id);
      if (activeTabElement)
        document.getElementById("active-tab").style.top =
          activeTabElement.offsetTop - 25 + "px";
    },
  },
};
</script>

<style >
